  -webkit-backdrop-filter: blur(0.5rem);
  backdrop-filter: blur(0.5rem);
}

main {
  display: flex;
  justify-content: space-around;
  gap: 10px;
}

.row {
  height: 50px;
  display: flex;
}

.cell {
  width: 50px;
  height: 100%;
  border: 1px black solid;
}

.ship {
  background: blue;
  color: white;
}

.hit {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background: green;
  color: white;
}

.miss {
  background: red;
  color: white;
  border-radius: 50%;
  height: 100%;
  width: 100%;
}
